<template>
  <div>

    <b-card
      no-body
      class="mb-0"
    >
      <div class="m-2">

        <!-- Table Top -->
        <b-row>

          <!-- Per Page -->
          <b-col
            cols="12"
            md="6"
            class="d-flex align-items-center justify-content-start mb-1 mb-md-0"
          >
            <label>Mostrar</label>
            <v-select
              v-model="perPage"
              :options="pageOptions"
              :clearable="false"
              class="per-page-selector d-inline-block mx-50"
            />
            <label>registros</label>
          </b-col>

          <!-- Search -->
          <b-col
            cols="12"
            md="6"
          >
            <div class="d-flex align-items-center justify-content-end">
              <b-form-input
                v-model="filter"
                type="search"
                class="d-inline-block mr-1"
                placeholder="Buscar..."
                debounce="500"
              />
              <b-button
                variant="primary"
                @click="newItem"
              >
                <span class="text-nowrap">Agregar</span>
              </b-button>
            </div>
          </b-col>
        </b-row>

      </div>

      <!-- Main table element -->
      <b-table
        id="my-table"
        ref="refTablaNutricion"
        class="position-relative px-2"
        show-empty
        head-variant="light"
        hover
        small
        responsive
        :busy.sync="isBusy"
        :items="myProvider"
        :fields="fields"
        :current-page="currentPage"
        :empty-text="'No hay registros para mostrar'"
        :empty-filtered-text="'No hay registros que coincidan con su solicitud.'"
        :per-page="perPage"
        :filter="filter"
        :filter-included-fields="filterOn"
        :sort-by="sortBy"
        :sort-desc="sortDesc"
      >
        <template #table-busy>
          <div class="text-center text-danger my-2">
            <b-spinner class="align-middle" />
            <strong>Cargando...</strong>
          </div>
        </template>

        <template #cell(index)="data">
          {{ from + data.index }}
        </template>
        <template #cell(servidor_publico)="data">

          <b-media vertical-align="center">
            <template #aside>
              <b-avatar
                size="32"
                :variant="`light-${resolveUserServicioVariant(data.item.persona_servidor_publico.nombres)}`"
                :text="avatarText(data.item.persona_servidor_publico.nombres)"
              />
            </template>
            <b-link
              class="font-weight-bold d-block text-nowrap"
            >
              <label :class="fondoDark ? 'text-white' : ''"><strong>{{ data.item.persona_servidor_publico.nombre_completo }}</strong></label>
            </b-link>
          </b-media>
        </template>

        <!-- Column: Actions -->
        <template #cell(acciones)="data">
          <b-dropdown
            variant="link"
            no-caret
            :right="$store.state.appConfig.isRTL"
          >

            <template #button-content>
              <feather-icon
                icon="MoreVerticalIcon"
                size="16"
                class="align-middle text-body"
              />
            </template>
            <b-dropdown-item
              @click="editItem(data.item)"
            >
              <feather-icon icon="EditIcon" />
              <span class="align-middle ml-50">Editar</span>
            </b-dropdown-item>
            <b-dropdown-item
              @click="cargarEvidencias(data.item)"
            >
              <feather-icon icon="PrinterIcon" />
              <span class="align-middle ml-50">Evidencias</span>
            </b-dropdown-item>

            <b-dropdown-item
              @click="deleteItem(data.item)"
            >
              <feather-icon icon="TrashIcon" />
              <span class="align-middle ml-50">Eliminar</span>
            </b-dropdown-item>
          </b-dropdown>
        </template>

      </b-table>

      <div class="mx-2 mb-2">
        <b-row>
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-start"
          >
            <span class="text-muted">Mostrando registros del {{ from }} al {{ to }} de un total de {{ totalRows }} registros</span>

          </b-col>
          <!-- Pagination -->
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-end"
          >

            <b-pagination
              v-model="currentPage"
              :total-rows="totalRows"
              :per-page="perPage"
              first-number
              last-number
              class="mb-0 mt-1 mt-sm-0"
              prev-class="prev-item"
              next-class="next-item"
            >
              <template #prev-text>
                <feather-icon
                  icon="ChevronLeftIcon"
                  size="18"
                />
              </template>
              <template #next-text>
                <feather-icon
                  icon="ChevronRightIcon"
                  size="18"
                />
              </template>
            </b-pagination>

          </b-col>
        </b-row>
      </div>

    </b-card>
    <nutricion-form-modal
      :dialog.sync="dialog"
      :edited-item="editedItem"
      :vamos-crear.sync="vamosCrear"
      @actualizar-tabla="actualizarTabla"
    />
    <nutricion-evidencias-modal
      :persona="persona"
      :edited-item="editedItem"
      @refresh-data-visitas-table="actualizarTabla"
    />
  </div>
</template>

<script>
import {
  BCard, BTable, BSpinner, VBModal, BRow, BCol, BFormInput, BDropdown, BDropdownItem, BPagination, BMedia,
  BAvatar, BButton,
  BLink,
} from 'bootstrap-vue'

import vSelect from 'vue-select'
import { onUnmounted } from '@vue/composition-api'
import store from '@/store'
// import stores from '@/store/index'

import Ripple from 'vue-ripple-directive'
import { avatarText } from '@core/utils/filter'
import nutricionStoreModule from './nutricionStoreModule'
import NutricionEvidenciasModal from './NutricionEvidenciasModal.vue'
import NutricionFormModal from './NutricionFormModal.vue'

export default {
  components: {
    BTable,
    NutricionFormModal,
    NutricionEvidenciasModal,
    BButton,
    vSelect,
    BSpinner,
    BCard,
    BRow,
    BCol,
    BFormInput,
    BDropdown,
    BDropdownItem,
    BPagination,
    BMedia,
    BAvatar,
    BLink,

    // componentes local
    // BuscarPersona,
  },
  directives: {
    'b-modal': VBModal,
    Ripple,
  },
  data() {
    return {
      dialog: false,
      vamosCrear: true,
      clonFrom: 0,
      persona: {},
      editedItem: {},
      isBusy: false,
      mostrar: false,
      items: [],
      from: 1,
      to: 1,
      totalRows: 1,
      currentPage: 1,
      perPage: 50,
      pageOptions: [50, 100, 300, 500],
      sortBy: 'id',
      sortDesc: false,
      sortDirection: 'desc',
      filter: '',
      filterOn: [],
      selectedPersona: {},
    }
  },
  computed: {
    fondoDark() {
      if (store.state.appConfig.layout.skin === 'dark') {
        return true
      }
      return false
    },
  },
  watch: {
    dialog(val) {
      // eslint-disable-next-line no-unused-expressions
      val || this.close()
    },
  },
  methods: {
    newItem() {
      this.dialog = true
    },
    close() {
      this.dialog = false // Cerramos el modal
      this.$nextTick(() => {
        this.editedItem = { ...this.defaultItem }
        this.vamosCrear = true
      })
    },
    actualizarTabla() {
      this.$refs.refTablaNutricion.refresh()
      this.$bvModal.hide('modal_comedor_nutricion')
    },
    agregarRegistroNutricion() {
      this.$bvModal.show('modal_comedor_nutricion')
    },
    editItem(item) {
      this.editedItem = { ...item }
      this.dialog = true // Para abrir el modal
      this.vamosCrear = false
    },
    // editarAsistencia(item) {
    //   const persona = item.persona_usuario
    //   this.editedItem = { ...item, persona }
    //   this.$bvModal.show('modal_comedor_nutricion')
    // },
    cargarEvidencias(item) {
      const persona = item.persona_usuario
      this.editedItem = { ...item, persona }
      this.$root.$emit('bv::show::modal', 'modal-soporte-evidencia', '#refModalSoporteEvidencia')
    },
    deleteItem(item) {
      this.$swal({
        title: 'Estas seguro?',
        text: 'No podrs revertir esto!',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Si, brralo!',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          store.dispatch('comedor-nutricion/deleteNutricion', item)
            .then(() => {
              this.$swal({
                icon: 'success',
                title: 'Eliminado!',
                text: 'Registro eliminado correctamente.',
                customClass: {
                  confirmButton: 'btn btn-success',
                },
              })
              this.actualizarTabla()
            }).catch(error => {
              this.$swal({
                title: 'Error',
                text: `${error}`,
                icon: 'error',
                customClass: {
                  confirmButton: 'btn btn-success',
                },
              })
            })
        } else if (result.dismiss === 'cancel') {
          this.$swal({
            title: 'Cancelado',
            text: 'Tu registro est seguro :)',
            icon: 'error',
            customClass: {
              confirmButton: 'btn btn-success',
            },
          })
        }
      })
    },

    async myProvider(ctx) {
      const promise = store.dispatch('comedor-nutricion/getNutricion', { queryParams: ctx })

      // Must return a promise that resolves to an array of items
      return promise.then(response => {
        // console.log('RESPONDE DATA:', response)
        const {
          data, total, from, to,
        } = response.data
        // Pluck the array of items off our axios response
        const items = data

        this.totalRows = total || 0
        // this.from = meta.pagination.count
        this.from = from || 0
        this.clonFrom = from
        this.to = to || 0
        // Must return an array of items or an empty array if an error occurred
        return items || []
      })
    },
  },
  setup() {
    const SOPORTE_SOCIAL_APP_STORE_MODULE_NAME = 'comedor-nutricion'

    // Register module
    if (!store.hasModule(SOPORTE_SOCIAL_APP_STORE_MODULE_NAME)) store.registerModule(SOPORTE_SOCIAL_APP_STORE_MODULE_NAME, nutricionStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(SOPORTE_SOCIAL_APP_STORE_MODULE_NAME)) store.unregisterModule(SOPORTE_SOCIAL_APP_STORE_MODULE_NAME)
    })

    const fields = [
      { key: 'index', label: 'N', sortable: true },
      { key: 'servidor_publico', label: 'Servidor publico', sortable: true },
      { key: 'motivo', label: 'motivo', sortable: true },
      { key: 'detalles', label: 'detalles', sortable: true },
      // { key: 'conclusiones', label: 'conclusiones', sortable: true },
      { key: 'acciones' },
    ]

    const resolveUserServicioVariant = () => 'primary'

    return {
      avatarText,
      fields,
      // nutricionStoreModule,
      resolveUserServicioVariant,
    }
  },
}
</script>
<style lang="scss" scoped>
.per-page-selector {
  width: 90px;
}
</style>

<style lang="scss">
@import '@core/scss/vue/libs/vue-select.scss';
</style>
